import {Component} from 'react';
import './PageStyle.css';

class Software extends Component{
    render(){
        return(
            <div className='soft'>
                <div id='soft-top'>
                    <p id='soft-title'>Software</p>
                </div>


                <div className="first">
                    <div id="first-left">
                        <div id="f_note">
                        <h2>first block</h2>
                        <br/>
                        <p>
                            this is a first block.
                            mouse hover event.
                            check the ref.
                        </p>
                        <input type="button" value="learn more"/>
                        </div>
                    </div>

                    <div id="first-right">
                        <div id="f_over">
                        <div id="f_over_over">
                            <p>
                            go to this page.<br/>
                            you can learn more helpful information.<br/>
                            if you want search other block.<br/>
                            </p>
                        </div>
                        </div>
                    </div>
                </div>


                <div className="second">
                    <div id="second-right">
                        <div id="s_note">
                        <h2>second block</h2>
                        <p>
                            this is second block.
                            check the event.
                            check the ref.
                        </p>
                        <input type="button" value="learn more"/>
                        </div>
                    </div>

                    <div id="second-left">
                        <div id="s_over">
                        <div id="s_over_over">
                            <p>
                            go to this page.<br/>
                            you can learn more helpful information.<br/>
                            if you want search other block.<br/>
                            </p>
                            <br/><br/>
                        </div>
                        </div>
                    </div>
                </div>

                <div id='footer-empty'></div>
            </div>
        );
    }
}

export default Software;